:root
{
  --Couleur1: #b4f5c4;/*hover button color*/
  --Couleur2: #262629;/**/
  --Couleur3: #929797;/**/
  --Couleur4: #fcfcfc;/**/
}

.App {
  
  background-image: url("https://images.unsplash.com/photo-1526304640581-d334cdbbf45e?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
  background-position: center;
}


.bouton {

  width: 15%;
  height: 8%;
  padding : 12px;
  border: 3px solid;
  border-color: black;
  border-radius: 15px;
  font-family:serif;
  font-size:2vw;
  transition: 0.1s;
  margin-left: 1%;
  margin-right: 1%;
  
  
  
  

}

.bouton:hover {


  cursor:pointer;
  background-color: var(--Couleur1);
  width: 17%;
  height: 8%;
  transition: 0.21s;
  box-shadow: 1px 1px 10px  var(--Couleur3);

  margin-left: 0%;
margin-right: 0%;

}

.buttonsContainer {

  background-color: var(--Couleur4);
  padding: 3.5%;
  margin-top: 35vh;
  text-align: center;

}

.endTitleContainer{padding: 3.5%;
  margin-top: 15vh;
  text-align: center;}

  .subTitle{

    font-size: 20pt;
    margin-top: -3vh;
    font-family:serif ;
    margin-bottom: 3vh;
  }


.goHomeContainer {

  margin-top: 3vh;
  margin-left: 1vh;
  
}

.wsjTitle{

  font-size: 40pt;
  margin-top: -3vh;
  font-family:serif ;
  margin-bottom: 3vh;
}

.pagesContent{

  background-color: var(--Couleur4);
  margin-left: 5%;
  height:101vh;
  width: 90vw;
  margin-top: -3vh;
  padding-top: 2vh ;
  overflow: scroll;

}


@media(min-aspect-ratio:4/3)
{

  .rulesParag {
    width: 30vw;
    height: max-content;
    margin-left: 7vw;
    margin-top: 7vh;
    text-align: justify;
  }

  .rulesParag2 {
    width: 30vw;
    height: max-content;
    margin-left: 7vw;
    margin-top: 7vh;
    text-align: justify;
    font-weight: bold;
  }
  
  .Colone {
    width: 30vw;
    margin: auto;
  }
  
  .parallele {
  
    display: flex;
  
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .prisonerImg { 
    width: 30vw;
  }



  .JournalColone {
    width: 70vw;
    margin: auto;
  }
  
  
  .JournalImg { 
    width: 70vw;
  }




  .interfaceContainer{
    width: 80vw;
    height: 170vh;
    margin: auto;
    background-color: var(--Couleur2) ;
    box-shadow: 1px 1px 10px  var(--Couleur2);
    border-radius: 15px;

  }


  .textContainer{

    
    padding: 1%;
    background-color: var(--Couleur3) ;
    width: auto;
    height: auto;
    text-align: center;

  }


  .balanceContainer{
    width: 30vw;
    text-align: center;
    height: auto;
    background-color: var(--Couleur3);
    margin : auto;}


    .totalBalance{font-family:serif;
      font-size:2vw;color: white;margin: auto;}

      .totalBalance:hover{color: rgb(108, 255, 95);}


      .playText{font-family:serif;
        font-size:1.5vw;color: white;}

        .playText:hover{color: rgb(86, 255, 71);}


        .creditsTEXT{

          font-size: 1vw;
          font-family: serif;
          margin-left: 1vw ;
          margin-right: 1vw;
          
        }

        .creditsTEXTcontainer{
          margin-top: 10vh;
          width: fit-content;
          margin-left: auto;
          margin-right: auto;
        }


        .chartsContainer{

          text-align: center;

        }
}

